.navi {
    background-color: #000000;
    padding: 20px;
    display: flex;
    justify-content: center;
  }
  
  .radio-input {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    background-color: black;
    padding: 10px;
    border-radius: 10px;
    z-index: 1;
    width: 300px;
    margin-left: 65%;
  }
  
  .radio-input input {
    display: none;
  }
  
  .radio-input .label {
    height: 80px;
    width: 90px;
    background: linear-gradient(to bottom, #333333, #242323);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 8px;
    transition: all 0.3s ease-in-out;
    border-top: 1px solid #4e4d4d;
    cursor: pointer;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    position: relative;
  }
  
  .radio-input .label:hover {
    transform: scale(1.05);
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.4);
  }
  
  .radio-input .label:has(input[type="radio"]:checked) {
    box-shadow: 0px 17px 5px 1px rgba(0, 0, 0, 0);
    background: linear-gradient(to bottom, #1d1d1d, #1d1d1d);
    border-top: none;
  }
  
  .radio-input .label::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 110%;
    height: 110%;
    border-radius: 10px;
    background: linear-gradient(
      to bottom,
      transparent 10%,
      transparent,
      transparent 90%
    );
    transition: all 0.3s ease-in-out;
    z-index: -1;
  }
  
  .radio-input .label:has(input[type="radio"]:checked)::before {
    background: linear-gradient(
      to bottom,
      transparent 10%,
      #cae2fd63,
      transparent 90%
    );
  }
  
  .radio-input .label .text {
    color: white;
    font-size: 13px;
    line-height: 12px;
    padding: 0px;
    font-weight: bold;
    text-transform: uppercase;
    transition: all 0.3s ease-in-out;
    text-shadow: -1px -1px 1px rgba(224, 224, 224, 0.1), 0px 2px 3px rgba(0, 0, 0, 0.3);
  }
  
  .radio-input .label input[type="radio"]:checked + .text {
    color: rgb(202, 226, 253);
    text-shadow: 0px 0px 12px #cae2fd;
  }
  .undo{
    color: #cae2fd;
    font-size: 50px;
    margin-top: 20px;
  }
  .u{
    margin-left: -200px;
  }
  .photo{
    width: 100px;
    margin-left: 100px;
  }